@import "../../../constants";

.relative_wrapper {
    position: relative;
}

.overflow_wrapper {
    width: 100%;
    overflow-x: auto;
}

.column_align_center {
    text-align: center !important;
    vertical-align: middle !important;
}

.blc_table {
    width: 100%;
    min-width: 1225px;
    table-layout: fixed;
}

.role_col {
    width: 170px;
}

.who_col {
    width: 80px;
}

.updated_col {
    width: 110px;
    border-left: 40px red solid;
}

.or_col {
    width: 95px;
}

.proj_plan_col,
.charter_col {
    width: 65px;
}

.tailoring_col,
.core_col {
    width: 75px;
}

.prog_mgr_col,
.bp_sales,
.lp_sales,
.les_learned {
    width: 70px;
}

.bp_plan_col,
.lp_plan {
    width: 50px;
}

.risks_col {
    width: 55px;
}

.comment {
    width: 400px;
}

.sticky_white {
    position: sticky;
    left: 0;
}

.sticky_white {
    background-color: white;
}

.controls {
    margin-top: -$default_margin;
}

.publish_btn {
    margin-bottom: 2 * $default_margin;
}

.help_icon {
    margin-left: 10px;
}